.product {
  padding: 30px;
  border: 1px solid #eee;
  border-radius: 15px;
  text-align: center;
  margin: 20px;
  position: relative;
  -webkit-box-shadow: 9px 10px 46px -15px #c8c8c8;
  -moz-box-shadow: 9px 10px 46px -15px #c8c8c8;
  box-shadow: 9px 10px 46px -15px #c8c8c8;
  transition: all ease-in-out 200ms;
  cursor: pointer;
  background-blend-mode: lighten;
  background-repeat: no-repeat !important;
  background-color: rgba(255, 255, 255, 0.6) !important;
  background-position: top center !important; }
  .product:hover {
    transition: all ease-in-out 200ms;
    -webkit-box-shadow: unset;
    -moz-box-shadow: unset;
    box-shadow: unset; }
  .product .productImg {
    width: auto;
    height: 70px;
    max-width: 100%;
    display: block;
    margin: 10px auto; }
  .product .name {
    font-weight: bold;
    margin: 10px;
    color: #444444; }
  .product .nutrientsSwitch {
    margin: 8px auto;
    color: #96ca2d; }
  .product .nutrients {
    height: 0;
    border-top: 1px solid #7fc6bc;
    padding-top: 5px;
    overflow: hidden;
    font-size: 12px;
    font-weight: bold;
    color: grey;
    text-align: left;
    transition: all ease-in-out 200ms; }
    .product .nutrients_info {
      font-size: 11px;
      color: #ccc; }
  .product .badge-times {
    padding: 7px 12px;
    position: absolute;
    top: 14px;
    left: 0;
    border-bottom-left-radius: 0;
    border-top-left-radius: 0; }
  .product .badge-category {
    background-color: white;
    width: 120px;
    border: 2px solid; }

li.product {
  text-align: left;
  border-radius: unset;
  margin: 10px 0; }
  li.product .productImg {
    max-width: 35px;
    height: auto;
    max-height: 35px;
    margin: auto 0 auto 12px; }
  li.product .productsInfo {
    width: 100%; }
    li.product .productsInfo .nutrientsBox {
      font-size: 13px; }
    li.product .productsInfo .tagsBox {
      text-align: right; }
      li.product .productsInfo .tagsBox span {
        margin: auto 2px; }
  li.product .name {
    font-weight: bold;
    margin: 10px;
    color: #444444; }
  li.product .badge-times {
    padding: 10px 6px; }
  li.product .badge-category {
    background-color: white;
    width: 120px;
    border: 2px solid; }
    li.product .badge-category .categoryImg {
      width: 20px; }

[data-category="meat"] .badge-category {
  border-color: #fda2a2 !important;
  color: #fda2a2 !important; }

[data-category="dairy"] .badge-category {
  border-color: lightgrey !important;
  color: lightgrey !important; }

[data-category="bread"] .badge-category {
  border-color: #b79569 !important;
  color: #b79569 !important; }

[data-category="vegetables"] .badge-category {
  border-color: #90bc90 !important;
  color: #90bc90 !important; }

[data-category="fruits"] .badge-category {
  border-color: #ffd9a4 !important;
  color: #ffd9a4 !important; }

[data-category="frozen-food"] .badge-category {
  border-color: #aee5e4 !important;
  color: #aee5e4 !important; }

[data-category="sweets"] .badge-category {
  border-color: #d9a7b8 !important;
  color: #d9a7b8 !important; }

[data-category="drinks"] .badge-category {
  border-color: #ffebce !important;
  color: #ffebce !important; }
